import Link from "next/link";
import { useState } from "react";

const Menu = () => {
  const [toggle, setToggle] = useState(false);
  const [style, setStyle] = useState("");

  const menuStyle = () => {
    "@media only screen and (max-width: 1024px) ";
  };

  const menuToggle = e => {
    if (!e) {
      setToggle(true);
    } else setToggle(false);
  };

  return (
    <nav className="menu" style={menuStyle()}>
      <button className="toggle" onClick={() => menuToggle(toggle)}>
        Show/hide
      </button>
      <Link href="/">
        <a>Home</a>
      </Link>
      <Link href="/frontend">
        <a>Frontend</a>
      </Link>
      <Link href="/design">
        <a>Design</a>
      </Link>
      <Link href="/resume">
        <a>Resum√©</a>
      </Link>
      <style jsx>{`
        .menu {
          display: flex;
          flex-direction: column;
          width: auto;
          height: 100%;
        }

        .toggle {
          display: none;
          position: absolute;
          right: 2rem;
          top: 0;
        }

        nav a {
          color: white;
          background-color: black;
          padding: 0.25rem;
          font-size: 1rem;
          transition: color 0.35s;
          margin-left: 0;
        }

        nav a:nth-child(n + 2) {
          margin-top: 0.5rem;
        }

        nav a:focus {
          background-color: white;
          color: black;
        }

        @media only screen and (max-width: 1024px) {
          .menu {
            position: absolute;
            width: 100vw;
            height: 100vh;
            background-color: white;
            justify-content: center;
          }

          .toggle {
            display: block;
          }

          nav a {
            text-align: center;
          }

          nav a:nth-child (1) {
            margin-top: 1rem;
          }
        }

        nav a:hover {
          background-color: white;
          color: black;
          transition: color 0.35s;
        }

        @media only screen and (orientation: portrait) {
          nav a {
            padding: 1.5rem 0.5rem;
          }
        }

        @media only screen and (max-width: 1024px) {
          nav a:nth-child (1) {
            margin-top: 0.5rem;
          }
        }
      `}</style>
    </nav>
  );
};

export default Menu;
